import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

const FavoriteFilm = ({ film, deleteFromFavorites }) => {
  return (
    <div>
      <h3> {film.title} </h3>
      <button type="button" onClick={() => deleteFromFavorites(film.id)}>
        Delete{' '}
      </button>
      <button type="button">
        {' '}
        <NavLink to={`/${film.title}/${film.id}`}> Show more </NavLink>{' '}
      </button>{' '}
    </div>
  );
};
FavoriteFilm.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
  }).isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
};
export default FavoriteFilm;
