import React from 'react';
import PropTypes from 'prop-types';

import FavoriteFilm from './FavoriteFilm/FavoriteFilm';

const FavoritesList = ({ favoritesList, deleteFromFavorites }) => {
  return (
    <section>
      {favoritesList.length > 0 ? (
        favoritesList.map(film => (
          <ul key={film.id}>
            <li>
              <FavoriteFilm
                film={film}
                deleteFromFavorites={deleteFromFavorites}
              />{' '}
            </li>{' '}
          </ul>
        ))
      ) : (
        <div> no fav </div>
      )}{' '}
    </section>
  );
};
FavoritesList.propTypes = {
  favoritesList: PropTypes.arrayOf().isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
};
export default FavoritesList;
